* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
html,body {
    height: 100%;
}
body {
    font-family: 'Baskervville', serif;
    font-family: 'Nanum Gothic', sans-serif;
    display: flex;
    flex-direction: column;
    min-height: 100vh;

}
.banner-container {
    background-color: #F9F9F9;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
/* .home-container { */
    /* min-height: 100%;
} */

button {
    background-color: #BA0C2F;
    color: white;
    font-size: 16px;
    border-radius: 4px;
    padding: 8px 18px;
    cursor: pointer;
    margin-top: 10px;
    transition: .3s;
  }
  button:hover {
    background-color: #fff;
    color: #BA0C2F;
    border-color: #BA0C2F;
  }
.ico {
    width: 64px;
    height: 64px;
}
.phone {
    width: 20px;
    height: 20px;
}
.slogan {
    align-self: center;
    text-align: center;
    display: none;
}
.phone-info{
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align:center;
}
.phone-info a {
    text-decoration: none;
    color: #000;
}
.phone-link {
    margin-left: 10px;
    font-size: 150%;
    transition-duration: .1s;
}
.phone-link:hover {
    color: #98001E;
}
.color1{
    color: #BA0C2F;
}
.color2{
    color: #EA5572;
}
.color3{
    color: #98001E;
}
.color4{
    color: #DB2A4D;
}
.navbar {
    font-size: 18px;
    background-color: #BA0C2F;
    border: 1px solid rgba(0, 0, 0, 0.2);
    padding-bottom: 10px;
    height: 50px;
}
.banner-container {
    font-size: 10px;
}
.main-nav {
    list-style-type: none;
    display: none;
    background-color: #BA0C2F;
    /* padding: 30px; */
}
.nav-links,
.logo {
    text-decoration: none;
    color: #fff;
    padding: 25px 16px;
    transition-duration: .3s;
}
.main-nav li {
    text-align: center;
    margin: 15px auto;
}
.logo {
    display: inline-block;
    font-size: 22px;
    margin-top: 10px;
    margin-left: 20px;
}
.navbar-toggle {
    position: absolute;
    top: 80px;
    right: 20px;
    cursor: pointer;
    color: #fff;
    font-size: 24px;
}
.active {
    display: block;
}
.nav-links:hover, .logo:hover {
    color: #98001E;
    background-color: #fff;
}
.bottom-half {
    display: flex;
}
/* IE 6 */
* html #footer {
    position:absolute;
    top:expression((0-(footer.offsetHeight)+(document.documentElement.clientHeight ? document.documentElement.clientHeight : document.body.clientHeight)+(ignoreMe = document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop))+'px');
 }
.footer {
    display: flex;
    flex-direction: column;
    padding: 10px;
    width: 100%;
    flex-shrink: 0;
    background-color: #BA0C2F;
    color: white;
    margin-top: 20px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    font-size: .8em;
}
.spacer {
    flex: 1;
}
.showingBtn {
    background-color: #2270DF;
    color: #fff;
    font-size: 14px;
    padding: 8px 18px;
    border-radius: 4px;
    border: 1px solid gray;
    margin-bottom: 24px;
    margin-top: 24px;
    transition-duration: .3s;
    cursor: pointer;
}
.showingBtn:hover {
    background-color: #fff;
    color: #2270DF;
    border-color: #2270DF;
}
.footer h3 {
    color: #CBC8D0;
}
.footer a {
    color: #fff;
    text-decoration: none;
    transition-duration: .3s;
}
.footer a:hover {
    color: #EA5572;
}
.glide__slides{
    width: 100%;
}
#arrowTop {
    height: 12px;
    width: 16px;
    color: #fff;
    position: fixed;
    right: 0;
    bottom: 0;
    margin-right: 11px;
    margin-bottom: 20px;
    cursor: pointer;
  }
#arrowTop::before {
    content: 'â–²';
    padding: 10px;
    opacity: .8;
    background-color: #BA0C2F;
  }
#map {
    align-self: center;
    width: 60vw;
    height: 300px;
}
@media screen and (max-width: 650px) {
    .nav-container {
        display: none;
    }
    .nav-container-m {
        height: 70px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        background-color: #BA0C2F;
    }
    .nav-container-m .nav-logo {
        -webkit-box-flex: 1;
        -ms-flex: 1;
        flex: 1;
    }
    .nav-container-m .nav-navbar {
        -webkit-box-flex: 0;
        -ms-flex: 0;
        flex: 0;
        opacity: .9;
        z-index: 999;
    }
    .nav-container-m .nav-navbar ul {
        -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
        position: fixed;
        right: 100%;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        background: #ffffff;
        width: 100%;
        height: 100%;
        overflow: auto;
        -webkit-transition: left .3s;
        -o-transition: left .3s;
        transition: left .3s;
        text-align:center;
        font-size: 3em;
        font-weight: bold;
    }
    .nav-container-m .nav-navbar ul li a {
        padding: 10px;
        font-size: 16px;
        height: auto;
        line-height: normal;
        color: #000;
        text-decoration: none;
    }
    .nav-container-m .nav-navbar .icon-bar {
        padding: 18px 8px;
        width: 50px;
        height: 60px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        cursor: pointer;
    }
    .nav-container-m .nav-navbar .icon-bar i {
        background-color: #ffffff;
        height: 2px;
    }
    .nav-container-m .nav-navbar ._Menus-show {
        right: 0;
        z-index: 99999;
    }
    .bottom-half {
        display: flex;
        flex-direction: column;
        padding: 10px;
    }
    .welcome {
        line-height: 1.6em;
    }
    .testimonials {
        margin-top: 20px;
    }
    .footer-right {
        margin-top: 20px;
    }
    .author {
        display: none;
    }
    .author2-container {
        margin-top: 20px;
        line-height: 1.6em;
    }
    .author2 {
        font-size: small;
    }
    .footer-left {
        line-height: 2em;
    }
    #rex-street {
        background-image: url('../assets/RexStreet/104.jpg');
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        height: 300px;
        width: 100vw;
    }
    #jackson-street {
        background-image: url('../assets/JacksonStreet/Jackson_Front_2.JPG');
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        height: 300px;
        width: 100vw;
    }
    #main-street {
        background-image: url('../assets/MainStreet/Main_Front.jpg');
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        height: 300px;
        width: 100vw;
    }
    .container {
        width: 100%;
        margin: 0;
    }
    #mainPic {
        display: none;
    }
    #map {
        width: 90%;
    }
}

@media screen and (min-width: 651px) {
    .nav-container-m {
        display: none;
    }
    .author2 {
        display: none;
    }
    #rex-street {
        display: none;
    }
}

@media screen and (min-width: 768px) {
    .container {
        /* min-height: calc(100vh - 200px); */
        flex: 1 0 auto;
    }
    .banner-container {
        font-size: 100%;
    }
    #map {
        width: 100%;
    }
    .ico {
        width: 96px;
        height: 96px;
    }
    .slogan {
        align-self: center;
        text-align: center;
        visibility: visible;
        display: inline;
    }
    .phone {
        width: 24px;
        height: 24px;
    }
    .navbar {
        display: flex;
        justify-content: center;
        padding-bottom: 0;
        height: 70px;
        align-items: center;
        /* background-image: linear-gradient(to bottom right, #EA5572, #98001E); */
    }
    .main-nav {
        display: flex;
        margin-right: 30px;
        flex-direction: row;
        justify-content: flex-end;
    }
    .main-nav li {
        margin: 0;
    }
    .nav-links {
        margin-left: 40px;
    }
    .logo {
        margin-top: 0;
    }
    .navbar-toggle {
        display: none;
    }
    .logo:hover,
    .nav-links:hover {
        /* color: rgba(255, 255, 255, 1); */
    }
    .top-half {
        margin-top: 30px;
        margin-bottom: 20px;
    }
    .bottom-half {
        flex-direction: row;
        justify-content: space-between;
        padding: 20px;
    }
    .welcome {
        display: flex;
        flex-direction: column;
        line-height: 2em;
        max-width: 60%;
        font-size: 140%;
    }
    .welcome-text {
        padding-top: 10px;
    }
    .testimonials {
        display: flex;
        flex-direction: column;
        max-width: 30%;
        background-color: #F5F5F5;
        padding: 10px;
        line-height: 1.6em;
    }
    .footer {
        flex-direction: row;
        justify-content: space-between;
    }
    .footer-left {
        line-height: 2em;
    }
    .footer-right {
        display: column;
        width: 33.3333%;
        line-height: 1.3em;
        align-self: center;
    }
    .author {
        /* align-self: flex-end; */
        font-size: small;
    }
    .f-disclaimer-text {
        padding-top: 20px;
        line-height: 1.5em;
    }
}